 * @Author: your name
 * @Date: 2021-01-07 22:20:52
 * @LastEditTime: 2021-01-15 03:44:44
 * @LastEditors: Please set LastEditors
 * @Description: In User Settings Edit
 * @FilePath: \things_wechat\pages\things\user\demand\freight\info\index.vue
-->
<template>
    <view class="order_freight_info">
        <!-- tabbar -->
        <cu-custom :refresh="preRrefresh"><block slot="content">货物详情</block></cu-custom>
        <scroll-view class="order_freight_info_scroll" :class="(orderInfo.status >= 1 && orderInfo.status < 5) || orderInfo.status === 13 || orderInfo.status === 4 || orderInfo.status === 9 ? 'btn-padding-bottom':''" scroll-y="true">
            <!-- 订单状态 -->
            <view class="flex justify-center align-start order_type_info">
                <view class="type_text">{{ orderInfo.status | demandFreightorder_format }}</view>
                <view class="type_tip_text">{{ orderInfo.status | demandFreightOrderTip_format }}</view>
            </view>
            <!-- 货车信息/货车选择 -->
            <view class="cu-list menu"  v-if="orderInfo.status === 2 || orderInfo.status === 3">
                <view class="cu-item arrow" @tap="openSelectCarModal">
                    <view class="content">
                        <text v-if="orderInfo.grabsUserOrder.length > 0" class="text-grey">选择货车</text>
                        <text v-else class="text-grey">暂无人抢单</text>
                    </view>
                </view>
            </view>
            <view class="cu-bar bg-white solid-bottom margin-top" v-if="orderInfo.status > 4">
				<view class="action">
					<text class="cuIcon-title text-orange "></text> 货车信息
				</view>
			</view>
            <view class="things_width_full order_info" v-if="orderInfo.status >= 4">
                <view class="order_single_info">司机：{{orderInfo.grabsUserOrder[0].grabsRealname}}</view>
                <view class="order_single_info">押运员：{{orderInfo.grabsUserOrder[0].grabsEscortName}}</view>
                <view class="order_single_info">车牌号：{{orderInfo.grabsUserOrder[0].grabsCarNumber}}</view>
                <view class="order_single_info">挂车牌号：{{orderInfo.grabsUserOrder[0].grabsListingCarNumber}}</view>
                <view class="order_single_info">装车地点：{{orderInfo.grabsUserOrder[0].grabsLoadingAddress}}</view>
                <view class="order_single_info">装车地点：{{orderInfo.grabsUserOrder[0].grabsUnloadingAddress}}</view>
            </view>
            <view class="cu-bar bg-white solid-bottom margin-top">
				<view class="action">
					<text class="cuIcon-title text-orange "></text> 货物信息
				</view>
			</view>
            <!-- 货物信息 -->
            <view class="things_width_full freight_info">
                <!-- 地址 -->
                <view class="flex justify-between align-center address_info">
                    <view class="address_info_left">
                        <view class="text-cut start_place">装车地点：{{orderInfo.loadingAddress}}</view>
                        <view class="text-cut arrive_place">卸车地点：{{orderInfo.unloadingAddress}}</view>
                    </view>
                    <view class="address_info_right" @tap="linkToGps">
                        <image class="gps_icon" src="./../../../../../../static/images/global/gps_icon.png" mode="widthFix" />
                    </view>
                </view>
                <view class="freight_basic_info">
                    <view class="order_num">订单编号：{{orderInfo.orderNo}}</view>
                    <view class="things_width_full flex justify-start align-center user_info">
                        <view class="imageBox">
                            <image class="user_cover" :src="orderInfo.avatarUrl" mode="widthFix"/>
                        </view>
                        <view class="name">{{orderInfo.orderNo}}</view>
                        <view class="flex justify-start align-center score">
                            <image class="score_icon" src="./../../../../../../static/images/global/score_icon.png" mode="widthFix" />
                            <view class="score_num">{{orderInfo.reputation}}</view>
                            <view class="identity_text">{{orderInfo.userType}}</view>
                        </view>
                    </view>
                    <view class="freight_single_info">运输总价：￥{{orderInfo.totalMoney}}</view>
                    <view class="freight_single_info">货物名称：{{orderInfo.goodsName}}</view>
                    <view class="freight_single_info">货物类型：{{orderInfo.goodsType}}</view>
                    <view class="freight_single_info">损耗范围：{{orderInfo.lossRange}}</view>
                    <view class="freight_single_info">重量：{{orderInfo.weight}}吨</view>
                    <view class="freight_single_info">货物介质：{{orderInfo.goodsMedium}}</view>
                    <view class="freight_single_info">开始时间：{{orderInfo.startTime}}</view>
                    <view class="freight_single_info">结束时间：{{orderInfo.endTime}}</view>
                    <view class="freight_single_info">发布时间：{{orderInfo.createTime}}</view>
                    <view class="freight_single_info">备注：{{orderInfo.remark?orderInfo.remark:'暂无'}}</view>
                </view>
            </view>
            <!-- 车主上传的单据 -->
            <view class="cu-bar bg-white solid-bottom margin-top receipt_bar" v-if="orderInfo.status >= 9">
				<view class="action">
					<text class="cuIcon-title text-orange "></text> 上传单据信息
				</view>
			</view>
            <view class="receipt_msg" v-if="orderInfo.status >= 9">
                <view class="car_single_info">损耗：{{orderInfo.loss_weight}}吨</view>
                <view class="things_width_full receipt_list">
                    <image class="receipt_item" :src="orderInfo.transport_pic.pic_path" :data-url="orderInfo.transport_pic.pic_path" mode="widthFix" @tap="previewImage" />
                </view>
            </view>
            <!-- 货主上传的支付单据 -->
            <view class="cu-bar bg-white solid-bottom margin-top receipt_bar" v-if="(orderInfo.status >= 5 && orderInfo.status <= 12) || orderInfo.status === 14">
				<view class="action">
					<text class="cuIcon-title text-orange "></text> 支付收据
				</view>
			</view>
            <view class="receipt_msg" v-if="(orderInfo.status >= 5 && orderInfo.status <= 12) || orderInfo.status === 14">
                <view class="things_width_full receipt_list">
                    <image class="receipt_item" :src="orderInfo.pay_pic.pic_path" :data-url="orderInfo.pay_pic.pic_path" mode="widthFix" @tap="previewImage" />
                </view>
            </view>
        </scroll-view>
        <!-- 按钮操作区域 -->
        <view class="things_width_full flex justify-end align-center btn_box" v-if="(orderInfo.status >= 1 && orderInfo.status < 5) || orderInfo.status === 13 || orderInfo.status === 4 || orderInfo.status === 9">
            <!-- 待确认 -->
            <button class="bg-white cu-btn cancle_btn" :disabled="buttonType" v-if="orderInfo.status >= 1 && orderInfo.status < 5" :data-id="orderInfo.orderId" @tap="cancelOrderFun">取消订单</button>
            <!-- 更新订单 -->
            <button class="bg-white cu-btn submit_btn" :disabled="buttonType" v-if="orderInfo.status === 13" :data-id="orderInfo.orderId" @tap="linkToUpdate">修改需求</button>
            <!-- 支付运费 -->
            <button class="bg-white cu-btn assess_btn" :disabled="buttonType" v-if="orderInfo.status === 4" :data-id="orderInfo.orderId" @tap="linkToPay">支付</button>
            <!-- 确认收货 -->
            <button class="cu-btn bg-white cancel_btn" :disabled="buttonType" v-if="item.status === 9" :data-id="item.orderId" @tap.stop="getCallFun">拒绝收货</button>
            <button class="bg-white cu-btn assess_btn" :disabled="buttonType" v-if="orderInfo.status === 9" :data-id="orderInfo.orderId" @tap="confirmReceiptFun">确认收货</button>
            <!-- 已完成 -->
            <!-- <button class="bg-white cu-btn assess_btn" v-if="orderInfo.status === 11" :data-id="orderInfo.orderId" @tap="linkToEvaluate">评价</button> -->
        </view>
        <!-- 选择货物弹框 -->
        <selectCar @closeSelectCarModal="closeSelectCarModal" @selectCarFun="selectCarFun" :modalShow="selectCarModal" :carList="orderInfo.grabsUserOrder"></selectCar>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>

<script>
import { getGoodsOrderDetail_api,demandCancelOrders_api,dispatchOrder_api,confirmReceiptGrabsGoods_api } from '@/api/index.js';
import { appData } from '@/utils/config.js';
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import storage from '@/utils/storage.js';
import { identity_format } from '@/utils/filters.js';
import checkForm from '@/utils/checkFormData.js';
import { demandFreightorder_format,demandFreightOrderTip_format } from '@/utils/filters.js';
import selectCar from '../../components/selectCar/selectCar.vue';
let that = null;
export default {
    components: { selectCar },
    data() {
        return {
            appBaseData:null,//全局配置信息
            userInfo: {},
            selectCarModal: false,// 选择货主弹框
            order_id: '',
            orderInfo: {},// 订单详情
            buttonType: false,
            preRrefresh: false,// 判断上一页是否要刷新
        }
    },
    watch: {
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    computed: {
        ...mapGetters(['base_selectOrderType','base_cancelOrderType']) 
    },
    // 过滤器
    filters: {
        // 订单状态
        demandFreightorder_format,
        demandFreightOrderTip_format
    },
    methods: {
        ...mapMutations([
            'modal',
            'base'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        clearPageFun(e) {
            that.getFreightOrderInfo();
        },
        /**打开选择货物弹框
         * @description: 
         * @param {*} e
         * @return {*}
         */
        openSelectCarModal(e) {
            if (that.orderInfo.grabsUserOrder.length > 0) {
                that.selectCarModal = true;
            }
        },
        /**关闭选择货物弹框
         * @description: 
         * @param {*} e
         * @return {*}
         */
        closeSelectCarModal(e) {
            that.selectCarModal = false;
        },
        /**选择货物
         * @description: 
         * @param {*} e
         * @return {*}
         */
        selectCarFun(e) {
            let params = {
                grabs_order_id: e.grabsOrderId,
                type: that.base_selectOrderType[0].value,
            };
            dispatchOrder_api(params,that).then(res=>{
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'派单成功'}});
                that.closeSelectCarModal();
                that.clearPageFun();
                that.preRrefresh = true;
            }).catch(err=>{
                console.log(err)
            })
        },
        /**拨打电话
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getCallFun(e) {
            uni.makePhoneCall({
                phoneNumber: '15626236839'
            });
        },
        /**确认收货
         * @description: 
         * @param {*} e
         * @return {*}
         */
        confirmReceiptFun(e) {
            let params = {
                release_order_id: e.currentTarget.dataset.id
            };
            that.buttonType = true;
            confirmReceiptGrabsGoods_api(params,that).then(res=>{
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'完成收货'}});
                that.clearPageFun();
                that.buttonType = false;
            }).catch(err=>{
                console.log(err);
                that.buttonType = false;
            })
        },
        /**取消订单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        cancelOrderFun(e) {
            let params = {
                order_id: e.currentTarget.dataset.id,
                type: that.base_cancelOrderType[0].value,
            };
            that.buttonType = true;
            demandCancelOrders_api(params,that).then(res=>{
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'取消成功'}});
                that.clearPageFun();
                that.preRrefresh = true;
                that.buttonType = false;
            }).catch(err=>{
                console.log(err);
                that.buttonType = false;
            })
        },
        /**跳转更新订单
         * @description: 
         * @param {*} e
         * @return {*}
         */
        linkToUpdate(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/user/demand/freight/updateOrder/index?goods_id=${e.currentTarget.dataset.id}`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
        },
        /**跳转到支付
         * @description: 
         * @param {*} e
         * @return {*}
         */
        linkToPay(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/user/demand/freight/pay/index?order_id=${e.currentTarget.dataset.id}`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
        },
        /**跳转到评价
         * @description: 
         * @param {*} e
         * @return {*}
         */
        linkToEvaluate(e) {
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/user/demand/freight/assess/index?id=${e.currentTarget.dataset.id}`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
        },
        /**跳转到gps
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToGps(e) {
            return false;
            that.buttonType = true;
            uni.navigateTo({
                url: `/pages/things/user/order/gps/index`,
                complete: function(res) {
                    that.buttonType = false;
                }
            });
        },
        /**抢货单详情
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getFreightOrderInfo(e) {
            let params = {
                order_id: that.order_id
            };
            getGoodsOrderDetail_api(params,that).then(res=>{
                that.orderInfo = res;
            }).catch(err=>{
                console.log(err)
            })
        },
        /**图片预览
         * @description: 
         * @param {*} e
         * @return {*}
         */
        previewImage(e) {
            console.log(e);
            // 预览图片
            uni.previewImage({
                urls: [e.currentTarget.dataset.url],
                current: e.currentTarget.dataset.url
            });
        }
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        if (!options.order_id || options.order_id == ''|| options.order_id == 'undefined') {
            that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'该订单不存在',linkUrl:1}});
            return false;
        }
        that.order_id = options.order_id;
    },
    onShow: function() {
        that.getFreightOrderInfo();
    }
}
</script>

<style lang="scss" scoped>
    @import "./index";
</style>